import WpsRegistrationService from '@/services/wps-registration/WpsRegistrationService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let WpsRegistrationTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultWpsRegistrationTableFilter: {
      type: String,
      default: null
    },
    defaultWpsRegistrationTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      wpsRegistrationTableData: [],
      wpsRegistrationTableFilterTimer: null,
      wpsRegistrationTableFilter: this.defaultWpsRegistrationTableFilter,
      wpsRegistrationTableOrderBy: this.defaultWpsRegistrationTableOrderBy,
      wpsRegistrationTableContainer: 'loading-container',
      wpsRegistrationTableBusy: false,
      wpsRegistrationTableTotalRows: 0,
      wpsRegistrationTableCurrentPage: 1,
      wpsRegistrationTablePerPage: 10
    }
  },
  computed: {
    wpsRegistrationTableNoDataText: function () {
      return this.wpsRegistrationTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    wpsRegistrationTableTotalPages: function () {
      return Math.ceil(this.wpsRegistrationTableTotalRows / this.wpsRegistrationTablePerPage)
    },
    wpsRegistrationTablePagingInfo: function () {
      return this.pagingInfo(this.wpsRegistrationTableCurrentPage, this.wpsRegistrationTablePerPage, this.wpsRegistrationTableTotalRows)
    }
  },
  methods: {
    loadWpsRegistrationTableData (loadingContainerId) {
      let self = this
      this.wpsRegistrationTableBusy = true
      this.wpsRegistrationTableContainer = loadingContainerId || this.wpsRegistrationTableContainer
      this.startLoading(this.wpsRegistrationTableContainer)
      WpsRegistrationService.wpsRegistrations({
        pageIndex: this.wpsRegistrationTableCurrentPage - 1,
        pageSize: this.wpsRegistrationTablePerPage,
        filter: this.wpsRegistrationTableFilter,
        orderByField: this.wpsRegistrationTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.wpsRegistrationTableContainer)
        self.wpsRegistrationTableBusy = false
        self.wpsRegistrationTableTotalRows = response.data.totalRows
        self.wpsRegistrationTableData = response.data.registrationList || []
      }).catch(function (error) {
        self.stopLoading(self.wpsRegistrationTableContainer)
        self.wpsRegistrationTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshWpsRegistrationTable () {
      this.loadWpsRegistrationTableData()
    },
    filterWpsRegistrationTable (filter) {
      let self = this
      if (this.wpsRegistrationTableFilter !== filter) {
        clearTimeout(this.wpsRegistrationTableFilterTimer)
        this.wpsRegistrationTableFilterTimer = setTimeout(() => {
          self.wpsRegistrationTableFilter = filter
          self.loadWpsRegistrationTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.wpsRegistrationTableFilterTimer)
      }
    },
    orderWpsRegistrationTable (orderBy, active) {
      this.wpsRegistrationTableOrderBy = (active ? '' : '-') + orderBy
      this.loadWpsRegistrationTableData()
    }
  },
  watch: {
    wpsRegistrationTableCurrentPage: function () {
      this.refreshWpsRegistrationTable()
    },
    wpsRegistrationTablePerPage: function () {
      this.refreshWpsRegistrationTable()
    }
  }
}

export default WpsRegistrationTableMixin
