<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="wpsRegistrationTableData" :noDataText="wpsRegistrationTableNoDataText" :sst="true" search @search="filterWpsRegistrationTable" @sort="orderWpsRegistrationTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadWpsRegistrationTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="wpsRegistrationTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ wpsRegistrationTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="wpsRegistrationTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="employerName">
            {{ this.$t('EMPLOYER_NAME') }}
          </vs-th>
          <vs-th sort-key="payerName">
            {{ this.$t('PAYER_NAME') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].employerName">
              {{ data[index].employerName }}
            </vs-td>
            <vs-td :data="data[index].payerName">
              {{ data[index].payerName | placeholder('-') }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="wpsRegistrationTableTotalPages" v-model="wpsRegistrationTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import WpsRegistrationTableMixin from '@/mixins/wps-registration/WpsRegistrationTableMixin'

export default {
  name: 'wps-registration-popup',
  mixins: [PopupMixin, WpsRegistrationTableMixin],
  mounted: function () {
    this.loadWpsRegistrationTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
