              <p class="info-row">
                <span>{{ $t('TOTAL_INDICATIVE_DEBIT_AMOUNT') }}:</span>
                <span>{{ amountDebit | placeholder('-') }}</span>
              </p>
            </div>
          </div>

          <div class="nv-row row hover" v-if="transferType.typeBehaviourEnumId === 'FT_TYPE_BULK_FCY'">
            <div class="nv-col w-full">
              <p class="info-row">
                <span>{{ $t('DEBIT_ACCOUNT') }}:</span>
                <span>{{ debitAccountNumber | placeholder('-') }}</span>
              </p>
              <p class="info-row">
                <span>{{ $t('PAYMENT_FILE') }}:</span>
                <span>{{ paymentFileName | placeholder('-') }}</span>
              </p>
              <p class="info-row">
                <span>{{ $t('PROCESSING_DATE') }}:</span>
                <span>{{ processingDate | formatUnixDate }}</span>
              </p>
              <p class="info-row">
                <span>{{ $t('SENDER_REFERENCE') }}:</span>
                <span>{{ senderReference | placeholder('-') }}</span>
              </p>
              <p class="info-row">
                <span>{{ $t('TOTAL_TRANSACTION_COUNT') }}:</span>
                <span>{{ transactionCount | placeholder('-') }}</span>
              </p>
              <p class="info-row">
                <span>{{ $t('TOTAL_INDICATIVE_DEBIT_AMOUNT') }}:</span>
                <span>{{ amountDebit | placeholder('-') }}</span>
              </p>
            </div>
          </div>

          <vs-table class="table-no-header mt-base mb-base" :data="currencySummaryList" :noDataText="$t('NO_DATA_FOUND')" v-if="isBulkTransfer">
            <template slot="thead">
              <vs-th sort-key="currencyId">
                {{ this.$t('CURRENCY') }}
              </vs-th>
              <vs-th sort-key="exchangeRate">
                {{ this.$t('EXCHANGE_RATE') }}
              </vs-th>
              <vs-th sort-key="amount">
                {{ this.$t('TOTAL_AMOUNT') }}
              </vs-th>
              <vs-th sort-key="amountDebit">
                {{ this.$t('TOTAL_DEBIT_AMOUNT') }}
              </vs-th>
              <vs-th sort-key="transactionCount">
                {{ this.$t('TRANSACTION_COUNT') }}
              </vs-th>
              <vs-th sort-key="valueDate">
                {{ this.$t('VALUE_DATE') }}
              </vs-th>
            </template>

            <template slot-scope="{data}">
              <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
                <vs-td :data="data[index].currencyId">
                  {{ data[index].currencyId | placeholder('-') }}
                </vs-td>
                <vs-td :data="data[index].exchangeRate">
                  {{ data[index].exchangeRate | currency('') | placeholder('-') }}
                </vs-td>
                <vs-td :data="data[index].amount">
                  {{ data[index].amount | currency('') | placeholder('-') }}
                </vs-td>
                <vs-td :data="data[index].amountDebit">
                  {{ data[index].amountDebit | currency('') | placeholder('-') }}
                </vs-td>
                <vs-td :data="data[index].transactionCount">
                  {{ data[index].transactionCount | placeholder('-') }}
                </vs-td>
                <vs-td :data="data[index].valueDate">
                  {{ data[index].valueDate | formatUnixDate }}
                </vs-td>
              </vs-tr>
            </template>
          </vs-table>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import EnumerationService from '@/services/commons/EnumerationService'
import ExchangeRateService from '@/services/exchange-rate/ExchangeRateService'
import CurrencyService from '@/services/nucleus/CurrencyService'
import FundsTransferService from '@/services/ft/FundsTransferService'
import AccountLookup from '@/views/account/account-root/AccountLookup'
import BeneficiaryLookup from '@/views/beneficiary/beneficiary-root/BeneficiaryLookup'
import CurrencyLookup from '@/views/nucleus/currency/CurrencyLookup'
import CreditCardLookup from '@/views/card/credit/credit-card-root/CreditCardLookup'
import WpsRegistrationLookup from '@/views/wps-registration/WpsRegistrationLookup'
import { FormWizard, TabContent } from 'vue-form-wizard'
import NvTree from '@/components/nv-tree/NvTree'
import Datepicker from 'vuejs-datepicker'
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import MarkdownItVue from 'markdown-it-vue'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

export default {
  name: 'funds-transfer-create',
  mixins: [AlertMixin, DatePickerMixin, LoadingMixin, ListLayoutMixin],
  components: {
    FormWizard,
    TabContent,
    AccountLookup,
    CurrencyLookup,
    CreditCardLookup,
    BeneficiaryLookup,
    WpsRegistrationLookup,
    NvTree,
    Datepicker,
    MarkdownItVue
  },
  data: function () {
    return {
      transferId: '',
      transferTypes: [],
      transferType: {},
      baseCurrencyId: '',
      baseCurrencyAbbreviation: '',
      debitCurrencyId: '',
      debitAccountNumber: '',
      creditAccountNumber: '',
      beneficiaryId: '',
      beneficiaryName: '',
      beneficiaryAccountNumber: '',
      amount: 0,
      amountDebit: 0,
      transferCurrencyId: '',
      cardId: '',
      cardNumber: '',
      transferCurrencyAbbreviation: '',
      transferDetails: '',
      senderReference: '',
      transferPurposes: [],
      transferPurposeId: '',
      chargesTypes: [],
      chargesTypeEnumId: 'FT_CHARGE_SHA',
      exchangeRate: null,
      convertedAmount: null,
      postingDate: null,
      valueDate: null,
      processingDate: null,
      salaryDate: null,
      paymentFile: null,
      paymentFileName: '',
      transactionCount: 0,
      employerId: '',
      employerName: '',
      payerId: '',
      payerName: '',
      payerLegalId: '',
      currencySummaryList: []
    }
  },
  computed: {
    templateDownloadUrl: function () {
      let baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : ''
      let appId = this.$store.getters.appId
      let customerId = this.$store.getters.activeExternalId.externalId
      return `${baseUrl}/apps/Connect/DownloadSecure/downloadFundsTransferTypeTemplate?sourceAppId=${appId}&customerId=${customerId}&typeId=${this.transferType.typeId}`
    },
    beneficiaryType: function () {
      if (this.transferType.typeBehaviourEnumId === 'FT_TYPE_SINGLE_BANK') {
        return 'BEN_TYPE_BANK'
      } else if (this.transferType.typeBehaviourEnumId === 'FT_TYPE_SINGLE_LOCAL') {
        return 'BEN_TYPE_LOCAL'
      } else if (this.transferType.typeBehaviourEnumId === 'FT_TYPE_SINGLE_OVERSEAS') {
        return 'BEN_TYPE_OVERSEAS'
      } else {
        return null
      }
    },
    transferPurposeName: function () {
      for (let i = 0; i < this.transferPurposes.length; i++) {
        if (this.transferPurposes[i].purposeId === this.transferPurposeId) {
          return this.transferPurposes[i].purposeName
        }
      }
      return null
    },
    chargesTypeDescription: function () {
      for (let i = 0; i < this.chargesTypes.length; i++) {
        if (this.chargesTypes[i].enumId === this.chargesTypeEnumId) {
          return this.chargesTypes[i].description
        }
      }
      return null
    },
    isSingleTransfer: function () {
      return Object.keys(this.transferType).length > 0 ? this.transferType.typeBehaviourEnumId.startsWith('FT_TYPE_SINGLE') : false
    },
    isBulkTransfer: function () {
      return Object.keys(this.transferType).length > 0 ? this.transferType.typeBehaviourEnumId.startsWith('FT_TYPE_BULK') : false
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      FundsTransferService.transferTypeTree({
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.transferTypes = response.data[0].children || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'FundsTransferChargesType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.chargesTypes = response.data.enumerationList || []
      })

      CurrencyService.currencyApps({
        lcyOnly: true
      }).then(function (response) {
        self.baseCurrencyId = response.data.currencyAppList[0].uomId
        self.baseCurrencyAbbreviation = response.data.currencyAppList[0].abbreviation
      })
    },
    loadFundsTransferPurposes () {
      let self = this
      FundsTransferService.transferPurposes({
        transferTypeId: this.transferType.typeId,
        transferCurrencyId: this.transferCurrencyId,
        pageNoLimit: true,
        orderByField: 'position,purposeName'
      }).then(function (response) {
        self.transferPurposes = response.data.transferPurposeList || []
        self.transferPurposeId = ''
      })
    },
    selectFundsTransferTypeNode (node) {
      this.transferId = ''
      this.creditAccountNumber = ''
      this.beneficiaryId = ''
      this.beneficiaryName = ''
      this.beneficiaryAccountNumber = ''
      this.amount = 0
      this.amountDebit = 0
      this.transferCurrencyId = ''
      this.transferCurrencyAbbreviation = ''
      this.cardId = ''
      this.cardNumber = ''
      this.transferDetails = ''
      this.senderReference = ''
      this.transferPurposeId = ''
      this.chargesTypeEnumId = 'FT_CHARGE_SHA'
      this.exchangeRate = ''
      this.convertedAmount = ''
      this.postingDate = ''
      this.valueDate = ''
      this.processingDate = ''
      this.salaryDate = ''
      this.paymentFile = ''
      this.paymentFileName = ''
      this.transactionCount = 0
      this.employerId = ''
      this.employerName = ''
      this.payerId = ''
      this.payerName = ''
      this.payerLegalId = ''

      this.transferType = node.data
      if (this.transferType.currencyFieldTypeEnumId === 'FT_CCY_FLD_LCY') {
        this.transferCurrencyId = this.baseCurrencyId
        this.transferCurrencyAbbreviation = this.baseCurrencyAbbreviation
      } else {
        this.transferCurrencyId = ''
        this.transferCurrencyAbbreviation = ''
      }

      if (this.transferType.chargesFieldTypeEnumId === 'FT_CHARGE_FLD_BEN') {
        this.chargesTypeEnumId = 'FT_CHARGE_BEN'
      } else if (this.transferType.chargesFieldTypeEnumId === 'FT_CHARGE_FLD_OUR') {
        this.chargesTypeEnumId = 'FT_CHARGE_OUR'
      } else if (this.transferType.chargesFieldTypeEnumId === 'FT_CHARGE_FLD_SHA') {
        this.chargesTypeEnumId = 'FT_CHARGE_SHA'
      } else {
        this.chargesTypeEnumId = null
      }

      this.loadFundsTransferPurposes()
    },
    selectDebitAccountRecord (record) {
      this.debitAccountNumber = record.accountNumber
      this.debitCurrencyId = record.currencyId
      if (this.transferType.currencyFieldTypeEnumId === 'FT_CCY_FLD_DEBIT') {
        this.transferCurrencyId = record.currencyId
        this.transferCurrencyAbbreviation = record.currencyAbbreviation
      }
    },
    selectCreditAccountRecord (record) {
      this.creditAccountNumber = record.accountNumber
      if (this.transferType.currencyFieldTypeEnumId === 'FT_CCY_FLD_CREDIT') {
        this.transferCurrencyId = record.currencyId
        this.transferCurrencyAbbreviation = record.currencyAbbreviation
      }
    },
    selectBeneficiaryRecord (record) {
      this.beneficiaryId = record.beneficiaryId
      this.beneficiaryName = record.beneficiaryName
      this.beneficiaryAccountNumber = record.iban || record.accountNumber
      if (this.transferType.currencyFieldTypeEnumId === 'FT_CCY_FLD_ALL' || this.transferType.currencyFieldTypeEnumId === 'FT_CCY_FLD_CREDIT' ||
          (this.transferType.currencyFieldTypeEnumId === 'FT_CCY_FLD_FCY' && record.preferredCurrencyId !== this.baseCurrencyId) ||
          (this.transferType.currencyFieldTypeEnumId === 'FT_CCY_FLD_LCY' && record.preferredCurrencyId === this.baseCurrencyId)
      ) {
        this.transferCurrencyId = record.preferredCurrencyId
        this.transferCurrencyAbbreviation = record.preferredCurrencyAbbreviation
      }
    },
    selectCardRecord (record) {
      this.cardId = record.cardId
      this.cardNumber = record.cardNumber
    },
    selectFundsTransferCurrencyRecord (record) {
      this.transferCurrencyId = record.uomId
      this.transferCurrencyAbbreviation = record.abbreviation
      this.transferPurposeId = ''
      this.loadFundsTransferPurposes()
    },
    selectWpsRegistrationRecord (record) {
      this.employerId = record.employerId
      this.employerName = record.employerName
      this.payerId = record.payerId
      this.payerName = record.payerName
      this.payerLegalId = record.payerLegalId
    },
    validateFundsTransferType () {
      return (this.transferType.typeId || null) !== null
    },
    validateFundsTransferDetails () {
      if (this.isSingleTransfer) {
        this.calculateValueDate()
        this.calculateExchangeRate()
      } else if (this.isBulkTransfer) {
        if (!this.paymentFile) {
          return false
        } else {
          this.createBulkFundsTransfer()
        }
      }
      return true
    },
    calculateValueDate () {
      let self = this
      this.startLoading('loading-container')
      FundsTransferService.calculateValueDate({
        transferTypeId: this.transferType.typeId,
        transferCurrencyId: this.transferCurrencyId,
        debitCurrencyId: this.debitCurrencyId,
        amount: this.amount
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.postingDate = response.data.postingDate
        self.valueDate = response.data.valueDate
      }).catch(function () {
        self.stopLoading('loading-container')
      })
    },
    calculateExchangeRate () {
      let self = this
      this.startLoading('loading-container')
      ExchangeRateService.convert({
        debitCurrencyId: this.debitCurrencyId,
        creditCurrencyId: this.transferCurrencyId,
        creditAmount: this.amount
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.exchangeRate = response.data.exchangeRate
        self.convertedAmount = response.data.convertedAmount
      }).catch(function () {
        self.stopLoading('loading-container')
      })
    },
    handleFileUpload () {
      this.paymentFile = this.$refs.paymentFile.files[0]
      this.paymentFileName = this.$refs.paymentFile.files[0].name
    },
    createFundsTransfer (nextAction) {
      if (this.isSingleTransfer) {
        this.createSingleFundsTransfer(nextAction)
      } else {
        this.proposeBulkFundsTransfer(nextAction)
      }
    },
    createSingleFundsTransfer (nextAction) {
      let self = this
      this.startLoading('loading-container')
      FundsTransferService.createSingleFundsTransfer({
        typeId: this.transferType.typeId,
        debitAccountNumber: this.debitAccountNumber,
        creditAccountNumber: this.creditAccountNumber,
        beneficiaryId: this.beneficiaryId,
        cardId: this.cardId,
        currencyId: this.transferCurrencyId,
        amount: this.amount,
        transferDetails: this.transferDetails,
        transferPurposeId: this.transferPurposeId,
        chargesTypeEnumId: this.chargesTypeEnumId,
        employerId: this.employerId,
        payerId: this.payerId,
        payerLegalId: this.payerLegalId
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else {
            self.$router.push({ name: 'BrowseFundsTransferWorkflowInstances' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    createBulkFundsTransfer () {
      let self = this
      this.startLoading('loading-container')
      let formData = new FormData()
      formData.append('typeId', this.transferType.typeId)
      formData.append('debitAccountNumber', this.debitAccountNumber)
      formData.append('currencyId', this.transferCurrencyId)
      formData.append('senderReference', this.senderReference)
      formData.append('transferDetails', this.transferDetails)
      formData.append('chargesTypeEnumId', this.chargesTypeEnumId)
      formData.append('paymentFile', this.paymentFile)
      formData.append('processingDate', this.getTime(this.processingDate))
      formData.append('salaryDate', this.getTime(this.salaryDate))
      formData.append('employerId', this.employerId)
      formData.append('payerId', this.payerId)
      formData.append('payerLegalId', this.payerLegalId)
      FundsTransferService.createBulkFundsTransfer({
        formData: formData
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          self.transferId = response.data.transferId
          self.transactionCount = response.data.transactionCount
          self.amountDebit = response.data.amountDebit
          self.currencySummaryList = response.data.currencySummaryList
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    proposeBulkFundsTransfer (nextAction) {
      let self = this
      this.startLoading('loading-container')
      FundsTransferService.proposeBulkFundsTransfer({
        transferId: this.transferId
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else {
            self.$router.push({ name: 'BrowseFundsTransferWorkflowInstances' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    refreshBulkFundsTransfer () {
      let self = this
      FundsTransferService.refreshBulkFundsTransfer({
        transferId: this.transferId
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          self.currencySummaryList = response.data.currencySummaryList || []
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
