<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="beneficiaryTableData" :noDataText="beneficiaryTableNoDataText" :sst="true" search @search="filterBeneficiaryTable" @sort="orderBeneficiaryTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="refreshBeneficiaryTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="beneficiaryTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ beneficiaryTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="beneficiaryTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="beneficiaryId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="beneficiaryName">
            {{ this.$t('BENEFICIARY_NAME') }}
          </vs-th>
          <vs-th sort-key="accountNumber">
            {{ this.$t('ACCOUNT_NUMBER') }}
          </vs-th>
          <vs-th sort-key="typeName">
            {{ this.$t('BENEFICIARY_TYPE') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].beneficiaryId">
              {{ data[index].beneficiaryId }}
            </vs-td>
            <vs-td :data="data[index].beneficiaryName">
              {{ beneficiaryName(data[index]) | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].accountNumber">
              {{ beneficiaryAccountNumber(data[index]) | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].typeName">
              {{ data[index].typeName | placeholder('-') }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="beneficiaryTableTotalPages" v-model="beneficiaryTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import BeneficiaryTableMixin from '@/mixins/beneficiary/BeneficiaryTableMixin'

export default {
  name: 'beneficiary-popup',
  mixins: [BeneficiaryTableMixin, PopupMixin],
  mounted: function () {
    this.loadBeneficiaryTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
